import React, { useRef } from 'react';
import { View, Text, StyleSheet, FlatList, Dimensions, Animated } from 'react-native';
import Image from 'react-native-scalable-image';
import fakeProducts from '../../../dev/fakeProducts';

const { width: screenWidth } = Dimensions.get('window');
const ITEM_WIDTH = screenWidth * 0.8;
const ITEM_SPACING = (screenWidth - ITEM_WIDTH) / 2;

type ItemProps = {
  item: any;
  index: number;
  scrollX: Animated.Value;
};

const Item = ({ item, index, scrollX }: ItemProps) => {
  const inputRange = [(index - 1) * ITEM_WIDTH, index * ITEM_WIDTH, (index + 1) * ITEM_WIDTH];

  const opacity = scrollX.interpolate({
    inputRange,
    outputRange: [0.3, 1, 0.3],
  });

  const scale = scrollX.interpolate({
    inputRange,
    outputRange: [0.7, 1, 0.7],
  });

  return (
    <View style={{ width: ITEM_WIDTH, justifyContent: 'center', alignItems: 'center' }}>
      <View
        style={[
          styles.itemContainer,
          {
            shadowColor: '#000',
            shadowOffset: {
              width: 0,
              height: 1,
            },
            shadowOpacity: 0.2,
            shadowRadius: 1.41,
            elevation: 2,
          },
        ]}
      >
        <View style={styles.imageContainer}>
          <Image
            source={{ uri: item.image }}
            width={120}
            borderTopLeftRadius={8}
            borderBottomLeftRadius={8}
          />
        </View>
        <View style={styles.textContainer}>
          <Text style={{ fontSize: 16, fontWeight: 'bold' }}>{item.name}</Text>
        </View>
      </View>
    </View>
  );
};

export default function ProductCarousel() {
  const scrollX = useRef(new Animated.Value(0)).current;

  return (
    <View style={styles.container}>
      <Animated.FlatList
        data={fakeProducts}
        pagingEnabled
        keyExtractor={(item) => item.id.toString()}
        horizontal
        showsHorizontalScrollIndicator={false}
        snapToInterval={ITEM_WIDTH + ITEM_SPACING * 2}
        decelerationRate={0}
        bounces={false}
        onScroll={Animated.event([{ nativeEvent: { contentOffset: { x: scrollX } } }], {
          useNativeDriver: true,
        })}
        renderItem={({ item, index }) => <Item item={item} index={index} scrollX={scrollX} />}
        contentContainerStyle={styles.contentContainer}
      />
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    paddingVertical: 10,
  },
  contentContainer: {
    paddingHorizontal: ITEM_SPACING,
  },
  itemContainer: {
    flexDirection: 'row',
    borderRadius: 8,
    borderWidth: 0.4,
    backgroundColor: 'white',
    padding: 10,
  },
  imageContainer: {
    flex: 1,
  },
  textContainer: {
    flex: 2,
  },
});
