import React from 'react';
import { CardStyleInterpolators, createStackNavigator } from '@react-navigation/stack';
import CartScreen from '../scenes/AppScenes/CartScreen';
import { appScreens } from '../utils/appScreens';
import BottomTabNavigator from './bottomTabNavigator';
import ProductDetails from '../scenes/AppScenes/ProductDetails';



export default function AppNavigator() {
    const Stack = createStackNavigator();
    return (
        <Stack.Navigator
            screenOptions={{
                headerShown: false,
                cardStyleInterpolator: CardStyleInterpolators.forHorizontalIOS
            }}
        >
            <Stack.Screen name={"Tabs"} component={BottomTabNavigator} />

            <Stack.Screen name={appScreens.CartScreen.name} component={CartScreen} />
            <Stack.Screen name={appScreens.ProductDetails.name} component={ProductDetails} />

        </Stack.Navigator>
    );
}
